/* Leather Goods Page Styles */
.servicePage {
    background-color: #ffffff;
    padding: 4rem 2rem;
    text-align: center;
}

.serviceContainer {
    max-width: 1200px;
    margin: 0 auto;
}

.artisanHeading {
    font-size: 4rem;
    font-family: 'Cinzel', serif;
    color: #0A2540;
    margin-bottom: 10px;
    font-weight: normal;
}

.artisanDescriptionContainer {
    max-width: 900px;
    margin: 0 auto;
    padding: 0 2rem;
}

.artisanDescription {
    font-size: 1.4rem;
    color: #666;
    line-height: 1.6;
}

/* Centered Underline for Articles Heading */
.articlesHeading {
    font-size: 2.5rem;
    font-family: 'Cinzel', serif;
    margin-bottom: 2rem;
    font-weight: normal;
    position: relative;
    text-align: center;
    display: inline-block;
    padding-bottom: 10px;
}

.articlesHeading::after {
    content: '';
    position: absolute;
    width: 50%;
    left: 25%;
    bottom: -5px;
    height: 3px;
    background-color: #0A2540;
    border-radius: 2px;
}

/* Articles Gallery - 4 Columns */
.articlesGallery {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 20px;
    justify-items: center;
    overflow-x: auto;
    padding-bottom: 20px;
}

/* Article Item - Container for Image and Name */
.articleItem {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 280px;
    border-radius: 10px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
    overflow: hidden;
}

/* Article Image */
.articleImage {
    width: 100%;
    height: 400px;
    object-fit: cover;
    border-radius: 10px;
    transition: none;
}

/* Article Name Below the Image */
.articleName {
    font-family: 'Cinzel', serif;
    font-size: 1.2rem;
    color: #0A2540;
    font-weight: 500;
    text-align: center;
    margin-top: 15px;
    padding: 0 10px 15px;
}

/* Responsive Design for Tablets */
@media (max-width: 1024px) {
    .articlesGallery {
        grid-template-columns: repeat(2, 1fr);
    }

    .articlesHeading::after {
        width: 60%;
        left: 20%;
    }
}

/* Responsive Design for Mobile (max-width: 768px) */
@media (max-width: 768px) {
    .servicePage {
        padding: 2rem 1rem;
        text-align: center;
    }
    
    .artisanHeading {
        font-size: 3rem;
    }
    
    .artisanDescription {
        font-size: 1.2rem;
    }
    
    .articlesGallery {
        grid-template-columns: 1fr;
        gap: 20px;
    }
    
    .articleItem {
        width: 80%;
        margin: 0 auto;
    }
}
